/*** Scroll Top style ***/
.scroll-top {
    z-index          : 99999;
    width            : 50px;
    height           : 50px;
    right            : 25px;
    bottom           : 25px;
    color            : white;
    display          : none;
    cursor           : pointer;
    position         : fixed;
    font-size        : 18px;
    border-radius    : 50%;
    background       : #FC5546;
    animation        : pulse 2s infinite;
    -webkit-animation: pulse 2s infinite;
}